import { useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { useTranslation } from 'react-i18next'
import {
    Zap,
    Upload,
    FileText,
    ChevronUp,
    ChevronDown,
    Sparkles,
    Download,
    Loader2,
    CheckCircle
} from 'lucide-react'

interface QuickActionsProps {
    onSmartDispatch?: () => void
    onBatchImport?: () => void
    onExportReport?: () => void
}

export function QuickActions({ onSmartDispatch, onBatchImport, onExportReport }: QuickActionsProps) {
    const { t } = useTranslation()
    const [isExpanded, setIsExpanded] = useState(true)
    const [activeAction, setActiveAction] = useState<string | null>(null)
    const [actionCompleted, setActionCompleted] = useState<string | null>(null)

    const handleAction = async (actionId: string, callback?: () => void) => {
        setActiveAction(actionId)
        // Simulate async operation
        await new Promise(resolve => setTimeout(resolve, 1500))
        setActiveAction(null)
        setActionCompleted(actionId)
        setTimeout(() => setActionCompleted(null), 2000)
        callback?.()
    }

    const actions = [
        {
            id: 'smart-dispatch',
            label: t('transit.smartDispatch'),
            icon: Sparkles,
            color: 'from-violet-500 to-purple-600',
            description: t('transit.smartDispatchDescription'),
            onClick: () => handleAction('smart-dispatch', onSmartDispatch)
        },
        {
            id: 'batch-import',
            label: t('transit.batchImport'),
            icon: Upload,
            color: 'from-blue-500 to-cyan-600',
            description: t('transit.batchImportDescription'),
            onClick: () => handleAction('batch-import', onBatchImport)
        },
        {
            id: 'export-report',
            label: t('transit.exportReport'),
            icon: FileText,
            color: 'from-emerald-500 to-green-600',
            description: t('transit.exportReportDescription'),
            onClick: () => handleAction('export-report', onExportReport)
        }
    ]

    return (
        <motion.div
            initial={{ y: 20, opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            className="mt-6"
        >
            <div className="bg-white dark:bg-dark-800 rounded-xl border border-slate-200 dark:border-dark-600 shadow-lg overflow-hidden">
                {/* Title bar */}
                <button
                    onClick={() => setIsExpanded(!isExpanded)}
                    className="w-full px-4 py-3 flex items-center justify-between bg-slate-50 dark:bg-dark-700 hover:bg-slate-100 dark:hover:bg-dark-600 transition-colors"
                >
                    <div className="flex items-center gap-2 text-slate-700 dark:text-slate-300">
                        <Zap className="w-4 h-4 text-primary-500" />
                        <span className="font-medium">{t('transit.quickActions')}</span>
                    </div>
                    {isExpanded ? <ChevronDown className="w-4 h-4 text-slate-400" /> : <ChevronUp className="w-4 h-4 text-slate-400" />}
                </button>

                <AnimatePresence>
                    {isExpanded && (
                        <motion.div
                            initial={{ height: 0, opacity: 0 }}
                            animate={{ height: 'auto', opacity: 1 }}
                            exit={{ height: 0, opacity: 0 }}
                            transition={{ duration: 0.2 }}
                            className="overflow-hidden"
                        >
                            <div className="p-4">
                                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                                    {actions.map((action) => (
                                        <motion.button
                                            key={action.id}
                                            whileHover={{ scale: 1.02 }}
                                            whileTap={{ scale: 0.98 }}
                                            onClick={action.onClick}
                                            disabled={activeAction !== null}
                                            className={`relative p-4 rounded-xl bg-gradient-to-r ${action.color} text-white shadow-lg hover:shadow-xl transition-shadow disabled:opacity-70 disabled:cursor-not-allowed overflow-hidden group`}
                                        >
                                            {/* 背景动画 */}
                                            <div className="absolute inset-0 bg-white/10 translate-x-full group-hover:translate-x-0 transition-transform duration-500" />

                                            <div className="relative flex items-center gap-3">
                                                <div className="p-2 bg-white/20 rounded-lg">
                                                    {activeAction === action.id ? (
                                                        <Loader2 className="w-6 h-6 animate-spin" />
                                                    ) : actionCompleted === action.id ? (
                                                        <CheckCircle className="w-6 h-6" />
                                                    ) : (
                                                        <action.icon className="w-6 h-6" />
                                                    )}
                                                </div>
                                                <div className="text-left">
                                                    <p className="font-semibold">{action.label}</p>
                                                    <p className="text-xs text-white/70">{action.description}</p>
                                                </div>
                                            </div>
                                        </motion.button>
                                    ))}
                                </div>
                            </div>
                        </motion.div>
                    )}
                </AnimatePresence>
            </div>
        </motion.div>
    )
}

export default QuickActions
